/* ErrorPage.css */
.error-page {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f8d7da;
    color: #721c24;
    font-family: 'Arial', sans-serif;
  }
  
  .error-content {
    text-align: center;
    background: #ffffff;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .error-content h1 {
    font-size: 4em;
    margin-bottom: 20px;
  }
  
  .error-content p {
    font-size: 1.2em;
    margin-bottom: 30px;
  }
  
  .retry-button {
    padding: 10px 20px;
    font-size: 1em;
    color: #ffffff;
    background-color: #d9534f;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .retry-button:hover {
    background-color: #c9302c;
  }
  